// ** Custom Components
import Sidebar from '@components/sidebar'
import Select from 'react-select'
import CreatableSelect from 'react-select/creatable'
// ** Icons Imports
import { Link } from 'react-feather'
import classnames from 'classnames'
// ** Reactstrap Imports
import { Form, Label, Badge, Button } from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
import { Fragment, useEffect, useState } from 'react'
import { loggedInUser, activeOrganization } from '@src/helper/sassHelper'
import moment from 'moment'
const userData = loggedInUser()
const activeOrg = activeOrganization()
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import toast from 'react-hot-toast'

const SidebarSendInvoice = (props) => {

  const colourStyles = {
    multiValue: (styles, { }) => {

      return {
        ...styles,
        backgroundColor: '#7367f0'
      }
    }
  }

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const { open, toggleSidebar, invoice } = props
  const [thankyouNote, setThankyouNote] = useState('')
  const [userOptions, setUserOptions] = useState([])
  const [clientOptions, setClientOptions] = useState([])
  const [toEmail, setToEmail] = useState([])
  const [ccEmail, setCCEmail] = useState([])

  const getOrganizationUsers = () => {
    axios.post('/organizationusers/dropdown').then((res) => {
      setUserOptions(res.data.organizationusers)
    }).catch(() => { })
  }

  const getCoatctInfo = (id) => {
    axios.post('contactinformations/list', { contactid: id }).then((res) => {
      setClientOptions(res.data.contactinformations)
    }).catch(() => { })
  }

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }

  }

  const getThankyouNotes = () => {
    axios.post('/emailtemplates/get', { slug: 'invoice-send-mail' }).then((res) => {
      const mailSub = res.data.emailtemplates.description
      const test = mailSub.replace('{invoice_date}', dateFormat(invoice.paymentdue)).replace('{service}', invoice.taskinvoiceitemsservicename).replace('recipient_name', invoice.contactperson).replace('{recipient_name}', invoice.contactperson).replace('{invoice_no}', invoice.uniqueno).replace('{amount}', invoice.totalamount).replace('{invoice_amount}', invoice.totalamount).replace('{your_name}', userData.name).replaceAll('{firm_name}', activeOrg.name).replace('{', '').replace('}', '')
      setThankyouNote(test)
    }).catch(() => { })
  }

  useEffect(() => {
    if (invoice.clientid !== undefined) {
      getCoatctInfo(invoice.clientid)
    }
    if (invoice.contactid !== undefined) {
      getCoatctInfo(invoice.contactid)
    }
    if (Object.keys(invoice).length > 0) {
      getOrganizationUsers()
      getThankyouNotes()
    }
  }, [invoice])

  const sendMail = () => {
    setBlock(true)
    const subject = `${invoice.uniqueno} Invoices Acknowledgement Mail`
    axios.post('/google/sendGmail', { content: thankyouNote, subject, toEmail, ccEmail, taskinvoiceId: invoice.id })
      .then(() => {
        toggleSidebar(false)
        setBlock(false)
        toast.success('Email send Successfully', {position:'bottom-right'})
      })
    console.log(() => { })
  }

  return (
    <Sidebar
      size='lg'
      open={open}
      title='Send Invoice'
      headerClassName='mb-1'
      contentClassName='p-0'
      bodyClassName='pb-sm-0 pb-3'
      toggleSidebar={toggleSidebar}
    >
      <UILoader blocking={block} loader={<Loader />}>
        <Form>
          <div className='mb-1'>
            <Label for='invoice-from' className='form-label'>
              To
            </Label>

            <CreatableSelect
              className={classnames('react-select')}
              classNamePrefix='select'
              isMulti
              options={clientOptions}
              onChange={(e) => { setToEmail(e.map(i => i.email)) }}
              getOptionLabel={(option) => `${option.name} - ${option.email}`}
              getOptionValue={(option) => option.email}
              styles={colourStyles}
            />

          </div>
          <div className='mb-1'>
            <Label for='invoice-to' className='form-label'>
              CC
            </Label>
            <Select
              className={classnames('react-select')}
              classNamePrefix='select'
              isMulti
              onChange={(e) => { setCCEmail(e.map(i => i.email)) }}
              options={userOptions}
              getOptionLabel={(option) => `${option.name} - ${option.email}`}
              getOptionValue={(option) => option.email}
              styles={colourStyles}
            />
          </div>
          <div className='mb-1'>
            <Label for='invoice-message' className='form-label'>
              Message
            </Label>
            <div contentEditable={true} dangerouslySetInnerHTML={{ __html: thankyouNote }} className='border border-1 rounded-2 maxheight-400'>
            </div>
          </div>
          <div className='mb-1'>
            <Badge color='light-primary'>
              <Link className='me-50' size={14} />
              <span className='align-middle'>Invoice Attached</span>
            </Badge>
          </div>
          <div className='d-flex flex-wrap mt-2'>
            <Button className='me-1' color='primary' onClick={() => { sendMail() }} >
              Send
            </Button>
            <Button color='secondary' outline onClick={toggleSidebar}>
              Cancel
            </Button>
          </div>
        </Form>
      </UILoader>
    </Sidebar>
  )
}

export default SidebarSendInvoice
