// ** React Imports
import { Link } from 'react-router-dom'
import { useState, useEffect, Fragment } from 'react'
import axios from '@src/configs/axios/axiosConfig'
// ** Third Party Components
import { Can } from '@src/utility/context/Can.js'
import ReactPaginate from 'react-paginate'
import Avatar from '@components/avatar'
import Select from 'react-select'
import {
  Eye, Send, Edit, Copy, Trash, Download, MoreVertical, Trash2
} from 'react-feather'
// ** Reactstrap Imports
import {
  Button, Input, Row, Col, Card, CardBody, Label, DropdownToggle, DropdownMenu, Badge, UncontrolledTooltip, UncontrolledDropdown, DropdownItem, Table
} from 'reactstrap'
import SendInvoiceSidebar from '../shared-sidebar/SidebarSendInvoice'
import { getData, deleteInvoice, updateBulk, exortProposal } from '../store'
import { useDispatch, useSelector } from 'react-redux'
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { selectThemeColors } from '@utils'
import moment from 'moment'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
const MySwal = withReactContent(Swal)

const CustomHeader = ({ handleFilter, value, handlePerPage, rowsPerPage, exportList, selectedIds, returnupdateFlg }) => {

  const dispatch = useDispatch()
  const statusUpdatefn = (status) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(updateBulk({ status, selectedIds }))
        returnupdateFlg(true)
        MySwal.fire({
          icon: 'success',
          title: `${status === -1 ? 'Deleted' : statusArr[status]}`,
          text: `Client has been ${status === -1 ? 'Deleted' : statusArr[status]}`,
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  return (
    <div className='invoice-list-table-header w-100 py-2 px-1'>
      <Row>
        <Col lg='6' className='d-flex align-items-center px-0 px-lg-1'>
          <div className='d-flex align-items-center me-2'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              className='form-control ms-50 pe-3'
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
          </div>
          <div>
            {selectedIds.length > 0 &&
              <Can I={'delete'} a={'invoice'}>
                <div>
                  <UncontrolledTooltip target={'deleteIcon'}> Delete</UncontrolledTooltip>
                  <Trash2 className='text-danger cursor-pointer' id='deleteIcon' onClick={() => { statusUpdatefn(-1) }} />
                </div>
              </Can>
            }
          </div>
        </Col>
        <Col
          lg='6'
          className='actions-right d-flex align-items-center justify-content-lg-end flex-lg-nowrap flex-wrap mt-lg-0 mt-1 pe-lg-1 p-0'
        >
          <div className='d-flex align-items-center'>
            <label htmlFor='search-invoice'>Search</label>
            <Input
              id='search-invoice'
              className='ms-50 me-2 w-100'
              type='text'
              value={value}
              onChange={e => handleFilter(e.target.value)}
              placeholder='Search Proposal'
            />
          </div>
          <Can I={'write'} a={'invoice'}>
            <Button tag={Link} to='/proposal/add' color='primary'>
              Add Proposal
            </Button>
          </Can>
          <UncontrolledDropdown className='ms-1'>
            <DropdownToggle color='secondary' caret outline>
              <MoreVertical className='font-small-4 me-50' />
            </DropdownToggle>
            <DropdownMenu>
              <Can I={'export'} a={'invoice'}>
                <DropdownItem onClick={() => { exportList() }}>
                  <Download className='font-small-4 me-50' />
                  <span className='align-middle'>Export</span>
                </DropdownItem>
              </Can>
            </DropdownMenu>
          </UncontrolledDropdown>
        </Col>
      </Row>
    </div>
  )
}

const ProposalList = () => {
  // ** Store vars
  // ** States
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const dispatch = useDispatch()
  const store = useSelector(state => state.invoice)

  // ** States
  const [value, setValue] = useState('')
  const [selectedData, setSelectedData] = useState({})
  const [sendSidebarOpen, setSendSidebarOpen] = useState(false)
  const [sort, setSort] = useState('desc')
  const [sortColumn, setSortColumn] = useState('createdon')
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const currentStatus = { id: 4, name: 'Draft' }
  const [currentTaskStatus, setCurrentTaskStatus] = useState({ id: '', name: 'SelectAll' })
  // ** Vars
  const statusOptionsArr = ['', 'To Do', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']
  const statusObj = [
    '',
    'light-primary',
    'light-secondary',
    'light-success',
    'light-danger',
    'light-warning',
    'light-info',
    'light-success'
  ]
  const statusOptions = [{ name: 'Select All', id: '' }, { name: 'To Do', id: 1 }, { name: 'In progress', id: 2 }, { name: 'Completed', id: 3 }, { name: 'Sent to Review', id: 6 }, { name: 'Request Changes', id: 7 }]
  const toggleSendSidebar = () => setSendSidebarOpen(!sendSidebarOpen)

  // ** renders client column
  const renderClient = row => {
    const stateNum = Math.floor(Math.random() * 6),
      states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
      color = states[stateNum]
    return <Avatar color={color} className='me-50' content={row.contactname !== null ? row.contactname.charAt(0) : ''} />

  }

  useEffect(async () => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: value,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        taskstatus: currentTaskStatus.id
      })
    )
    setBlock(false)
  }, [dispatch])

  const handleFilter = async val => {
    setValue(val)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        taskstatus: currentTaskStatus.id
      })
    )
    setBlock(false)
  }

  const handlePerPage = async e => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: value,
        sortColumn,
        page: currentPage,
        status: currentStatus.id,
        taskstatus: currentTaskStatus.id,
        perPage: parseInt(e.target.value)
      })
    )
    setBlock(true)
    setRowsPerPage(parseInt(e.target.value))
  }

  const exportList = async () => {
    setBlock(true)
    await dispatch(
      exortProposal({
        sort,
        sortColumn,
        q: value,
        status: currentStatus.id,
        taskstatus: currentTaskStatus.id
      })
    )
    setBlock(false)
  }

  // const handleSort = (column, sortDirection) => {
  //   setSort(sortDirection)
  //   setSortColumn(column.sortField)
  //   dispatch(
  //     getData({
  //       q: value,
  //       page: currentPage,
  //       sort: sortDirection,
  //       status: currentStatus.id,
  //taskstatus: currentTaskStatus.id,
  //       perPage: rowsPerPage,
  //       sortColumn: column.sortField
  //     })
  //   )
  // }

  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: value,
        sortColumn,
        status: currentStatus.id,
        taskstatus: currentTaskStatus.id,
        perPage: rowsPerPage,
        page: page.selected + 1
      })
    )
    setCurrentPage(page.selected + 1)
    setBlock(false)
  }

  const CustomPagination = () => {
    const count = Number((store.total / rowsPerPage).toFixed(0))

    return (
      <ReactPaginate
        nextLabel=''
        breakLabel='...'
        previousLabel=''
        pageCount={count || 1}
        activeClassName='active'
        breakClassName='page-item'
        pageClassName={'page-item'}
        breakLinkClassName='page-link'
        nextLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousLinkClassName={'page-link'}
        previousClassName={'page-item prev'}
        onPageChange={page => handlePagination(page)}
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        containerClassName={'pagination react-paginate justify-content-end p-1'}
      />
    )
  }

  const dataToRender = () => {
    const filters = {
      q: value,
      status: currentStatus.id,
      taskstatus: currentTaskStatus.id
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return []
    }
  }

  useEffect(() => {
    setSort('desc')
    setRowsPerPage(10)
    setSortColumn('createdon')

    dataToRender()
  }, [store.data])

  const deleteClientfun = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteInvoice(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Invoice has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }
  }

  const downloadPdf = (row) => {
    setBlock(true)
    axios.post('/pdfmaker/pdf', { id: row.id })
      .then((res) => {
        const downloadLink = document.createElement("a")
        const fileName = `${row.uniqueno}.pdf`
        downloadLink.href = res.data.pdf
        downloadLink.download = fileName
        downloadLink.click()
        document.removeChild(downloadLink)
        setBlock(false)
      })
      .catch((err) => {
        console.log(err)
        setBlock(false)
      })
  }

  const [selectedIds, setSelectedIds] = useState([])
  const selectedIdsFn = (val) => {

    if (selectedIds.indexOf(val) === -1) {
      setSelectedIds([...selectedIds, val])
    } else {
      const test = selectedIds
      const ind = test.indexOf(val)
      test.splice(ind, 1)
      setSelectedIds(test)
    }

  }

  const returnupdateFlg = (val) => {
    if (val) {
      setSelectedIds([])
      dataToRender()
    }
  }
  return (
    <div className='invoice-list-wrapper'>
      <Card>
        <CardBody>
          <Row>
            <Col md='4'>
              <Label for='status-select'>Task Status</Label>
              <Select
                theme={selectThemeColors}
                isClearable={false}
                className='react-select'
                classNamePrefix='select'
                options={statusOptions}
                value={currentTaskStatus}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={async data => {
                  setCurrentTaskStatus(data)
                  setBlock(true)
                  await dispatch(
                    getData({
                      sort,
                      sortColumn,
                      q: value,
                      page: currentPage,
                      perPage: rowsPerPage,
                      status: currentStatus.id,
                      taskstatus: data.id
                    })
                  )
                  setBlock(false)
                }}
              />
            </Col>
          </Row>
        </CardBody>
      </Card>
      <Card>
        <div className='invoice-list-dataTable react-dataTable'>
          <CustomHeader handlePerPage={handlePerPage} exportList={exportList} handleFilter={handleFilter} selectedIds={selectedIds} returnupdateFlg={returnupdateFlg} />
          <UILoader blocking={block} loader={<Loader />}>
            <Table className='table-hover'>
              <thead>
                <tr>
                  <th>

                  </th>
                  <th>Proposal ID</th>
                  <th>Customer</th>
                  <th>Proposal Items</th>
                  <th>Proposal Date</th>
                  <th>Total</th>
                  <th>Task Status</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {dataToRender().map((row, index) => {
                  return (
                    <tr key={index}>
                      <td>
                        {selectedIds.indexOf(row.id) > -1 && (

                          <Input type='checkbox' onClick={() => { selectedIdsFn(row.id) }} defaultChecked />

                        )
                        }{
                          selectedIds.indexOf(row.id) === -1 && (
                            <Input type='checkbox' onClick={() => { selectedIdsFn(row.id) }} />
                          )
                        }
                      </td>
                      <td><Link to={`/proposal/view/${row.id}`}>
                        <span className='text-body'>{`${row.uniqueno}`}
                        </span></Link></td>
                      <td>{
                        <Link to={`/proposal/view/${row.id}`}>
                          <div className='d-flex justify-content-left align-items-center'>
                            {renderClient(row)}
                            <div className='d-flex flex-column'>
                              <h6 className='user-name text-truncate mb-0'>{row.contactname}</h6>
                              <small className='text-truncate text-muted mb-0'>{row.contactemail}</small>
                            </div>
                          </div>
                        </Link>
                      }</td>
                      <td>
                        <Link to={`/proposal/view/${row.id}`}>
                          <span className='text-body'>{row.taskinvoiceitemsservicename}
                          </span></Link>
                      </td>
                      <td>
                        <Link to={`/proposal/view/${row.id}`}>
                          <span className='text-body'>{dateFormat(row.invoicedate)}
                          </span></Link></td>
                      <td><Link to={`/proposal/view/${row.id}`}><span className='text-body'>{row.totalamount || 0}
                      </span></Link></td>
                      <td>
                        <Badge color={statusObj[row.taskstatus]}>{statusOptionsArr[row.taskstatus]}</Badge>
                      </td>
                      <td>
                        <div className='column-action d-flex align-items-center'>
                          <Can I={'read'} a={'invoice'}>
                            <Send size={17} onClick={() => { toggleSendSidebar(); setSelectedData(row) }} />
                          </Can>
                          <Can I={'read'} a={'invoice'}>
                            <Link to={`/proposal/view/${row.id}`} id={`pw-tooltip-${row.id}`}>
                              <Eye size={17} className='mx-1' />
                            </Link>
                          </Can>
                          <UncontrolledTooltip placement='top' target={`pw-tooltip-${row.id}`}>
                            Preview Invoice
                          </UncontrolledTooltip>
                          <UncontrolledDropdown>
                            <DropdownToggle tag='span'>
                              <MoreVertical size={17} className='cursor-pointer' />
                            </DropdownToggle>
                            <DropdownMenu end>
                              <Can I={'export'} a={'invoice'}>
                                <DropdownItem tag='a' href='/' className='w-100' onClick={e => { e.preventDefault(); downloadPdf(row) }}>
                                  <Download size={14} className='me-50' />
                                  <span className='align-middle'>Download</span>
                                </DropdownItem>
                              </Can>
                              <Can I={'write'} a={'invoice'}>
                                <DropdownItem tag={Link} to={`/proposal/edit/${row.id}`} className='w-100'>
                                  <Edit size={14} className='me-50' />
                                  <span className='align-middle'>Edit</span>
                                </DropdownItem>
                              </Can>
                              <Can I={'delete'} a={'invoice'}>
                                <DropdownItem
                                  tag='a'
                                  href='/'
                                  className='w-100'
                                  onClick={e => {
                                    e.preventDefault()
                                    deleteClientfun(row.id)
                                  }}
                                >
                                  <Trash size={14} className='me-50' />
                                  <span className='align-middle'>Delete</span>
                                </DropdownItem>
                              </Can>
                              <Can I={'write'} a={'invoice'}>
                                <DropdownItem tag={Link} className='w-100' to={`/proposal/clone/${row.id}`}>
                                  <Copy size={14} className='me-50' />
                                  <span className='align-middle'>Duplicate</span>
                                </DropdownItem>
                              </Can>
                            </DropdownMenu>
                          </UncontrolledDropdown>
                        </div>
                      </td>
                    </tr>
                  )
                })
                }
              </tbody>
            </Table>
          </UILoader>
          <CustomPagination />
        </div>
        <SendInvoiceSidebar toggleSidebar={toggleSendSidebar} open={sendSidebarOpen} invoice={selectedData} />
      </Card >
    </div >
  )
}

export default ProposalList
